.post_container {
    width: 100%;
    height: 502px;
    background-color: #f4e2e2;
}

.heading_section {
    width: 76%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0px auto;
    padding: 20px 0 20px 27px;
    font-weight: bold;

}

.post_grid {
    width: 72%;
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 0 auto;
    column-gap: 30px;

}

.post {
    width: 483px;
    height: 356px;
    background-color: white;
    padding: 10% 18% 10% 10%;
}

.heading_post {
    font-family: 'Heebo', sans-serif;
    font-size: 26px;
    font-weight: 700;
    line-height: 38px;
    height: 87.53px;
    

}

.date_detail {
    font-family: 'Heebo', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 38px;
    height: 42.5px;
    letter-spacing: 2%;
}

.detail {
    font-family: 'Heebo', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 23.5px;
    height: 129.7px;
    padding-top: 5%;
}