.nav_container{
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 30px 7% 20px 7%;
    background-color: #cfa6a641;
}

.link_li{
    text-decoration: none;
    margin: 0 3%;
    font-weight: 600;
    padding: 5px 20px;
}

.li{
    text-decoration: none;
    list-style: none;
    font-family: 'Inter',sans-serif;
    
}

.link_li:hover{
    background-color: white;
    color: #cfa6a6ef;
}