.hero_section_container {
    height: 80vh;
    width: 100%;
    padding: 7% 10% 7% 10%;

}

.hero_section_grid {
    width: 90%;
    height: 308px;
    display: grid;
    grid-template-columns: 70% 30%;
    margin: 0 auto;

}


.hero_detail_section {
    padding: 0 30px 0 0;
}




.hero_h1 {
    font-size: 48px;
    font-weight: 900;
    line-height: 61px;
    font-family: 'Heebo', sans-serif;
}

@font-face {
    font-family: 'Heebo';
    src: url('../../app/fonts/Heebo-VariableFont_wght.ttf') format('truetype');

}

.hero_summary {
    font-family: 'Heebo', sans-serif;
    font-size: 15px;
    font-weight: 400;
    list-style: 23.5px;
    margin: 20px 0;

}


.download_btn {
    width: 205px;
    height: 50px;
    background-color: #f98585;
    color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
    border-radius: 3px;
    font-family: 'Heebo', sans-serif;
    font-weight: 500;
    font-size: 18px;

}








/*   Right section for image */











.hero_pic_section {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

}

.hero_image_div {
    border-radius: 50%;
    
}

.hero_image {
    width: 292px;
    height: 292px;
    border-radius: 50%;
    -webkit-box-shadow: -12px 11px 0 0.5px #AA9F9F4F;
    -moz-box-shadow: -12px 11px 0 0.5px #AA9F9F4F;
    box-shadow: -12px 11px 0 0.5px #AA9F9F4F;
}